.group {
  display: block;
  height: 100%;
  width: 100%;
  background-color: #cccccc;
}

.joinGroup {
  margin-top: 100rpx;
  height: 100rpx;
  width: 100%;
  line-height: 100rpx;
  font-size: 38rpx;
  background-color: #EFEFF4;
  text-align: center;
  color: #008808;
}

.group-list {
  margin-top: 50rpx;
  height: 150rpx;
  width: 100%;
  line-height: 100rpx;
  background-color: white;
  box-sizing: border-box;
  border-top: 1px solid black;
  border-bottom: 1px solid black;

  image {
    display: block;
    float: left;
    height: 100rpx;
    width: 100rpx;
    background-color: #EFEFF4;
    margin-top: 25rpx;
    margin-left: 50rpx;
  }

  .groupName {
    display: block;
    height: 30rpx;
    width: 50%;
    line-height: 30rpx;
    font-size: 28rpx;
    margin-top: 40rpx;
    margin-left: 170rpx;
  }

  .state {
    display: block;
    height: 30rpx;
    width: 50%;
    line-height: 30rpx;
    font-size: 28rpx;
    margin-top: 20rpx;
    margin-left: 170rpx;
    color: red;
  }
}